	module.exports = {"data":{"challengeNode":{"title":"Create Visual Direction by Fading an Element from Left to Right","description":["在本关里，你将要改变动画元素的<code>opacity</code>，使其在到达屏幕右侧时渐隐。","在展示的动画里，具有渐变背景的圆形元素在 50% 标记的<code>@keyframes</code>规则处向右移动。","<hr>","使用 id 选择器选择 id 为<code>ball</code>的元素，在<code>50%</code>里添加<code>opacity</code>属性并赋值 0.1，使其在向右移动时渐隐。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/applied-visual-design/create-visual-direction-by-fading-an-element-from-left-to-right","blockName":"Applied Visual Design","tests":[{"text":"50% 处<code>keyframes</code>规则应该设置<code>opacity</code>属性值为 0.1 以使其渐隐。","testString":"assert(code.match(/@keyframes fade\\s*?{\\s*?50%\\s*?{\\s*?(?:left:\\s*?60%;\\s*?opacity:\\s*?0?\\.1;|opacity:\\s*?0?\\.1;\\s*?left:\\s*?60%;)/gi), '50% 处<code>keyframes</code>规则应该设置<code>opacity</code>属性值为 0.1 以使其渐隐。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n\n  #ball {\n    width: 70px;\n    height: 70px;\n    margin: 50px auto;\n    position: fixed;\n    left: 20%;\n    border-radius: 50%;\n    background: linear-gradient(\n      35deg,\n      #ccffff,\n      #ffcccc\n    );\n    animation-name: fade;\n    animation-duration: 3s;\n  }\n\n  @keyframes fade {\n    50% {\n      left: 60%;\n      \n    }\n  }\n\n</style>\n\n<div id=\"ball\"></div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/applied-visual-design/animate-elements-continually-using-an-infinite-animation-count","id":"587d78a7367417b2b2512ae2"},"slug":"/responsive-web-design/applied-visual-design/create-visual-direction-by-fading-an-element-from-left-to-right"}}

